    <span class="token string single-quoted-string">'secret'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'f1c242f4f28f735d4687abb469072axx'</span><span class="token punctuation">,</span>

    <span class="token comment">// 下面为可选项</span>
